/**
 * 获取系统信息
 * @returns {Object} 系统信息
 */
export function getSystemInfo() {
  return uni.getSystemInfoSync()
}

/**
 * 创建选择器查询
 * @param {Object} context 上下文
 * @returns {Object} 选择器查询对象
 */
export function createSelectorQuery(context) {
  return uni.createSelectorQuery().in(context)
}

/**
 * 页面滚动到指定位置
 * @param {Object} options 选项
 */
export function pageScrollTo(options) {
  uni.pageScrollTo(options)
}